@font-face{font-family:'Montserrat';font-style:normal;font-weight:400;src:url("../fonts/Montserrat-Regular.ttf")}@font-face{font-family:'Montserrat';font-style:normal;font-weight:700;src:url("../fonts/Montserrat-Bold.ttf")}body{margin:0;font-family:'Montserrat';-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#5b5b5b;line-height:1.2;font-size:24px;background-color:#e5e5e5}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}img{display:block;max-width:100%;height:auto}h1,h2,h3,h4,h5,h6{margin-top:0}p{margin-top:0}.App{margin:0 auto;width:100%;text-align:center}.form-signin{margin:50px auto;padding:35px;width:570px;height:964px;box-shadow:5px 10px 30px rgba(0,0,0,0.05);border-radius:16px;background-color:#fff}.form-control{width:100%;padding:24px;margin-top:12px;margin-bottom:30px;font-size:18px;font-weight:400;color:#5b5b5b;border:1px solid #c1c1c1;border-radius:16px;background-color:transparent}.form-control::placeholder{font-size:18px}.form-control:active{border:1px solid #87d78b}
